import { createRouter, createWebHistory } from 'vue-router';

const router = createRouter({
  history: createWebHistory(),
  routes: [
    /*  */
    {
      path: '/:access_token?',
      name: 'HomeIndex',
      component: () => import('../pages/home/Home'),
      meta: { title: '' }
    },
    /*  */
    {
      path: '/home/search',
      name: 'HomeSearch',
      component: () => import('../pages/home/Search'),
      meta: { title: '' }
    },
    /*  */
    {
      path: '/home/chat',
      name: 'ConnectPerson',
      component: () => import('../pages/home/Chat'),
      meta: { title: '' }
    },
    {
      path: '/home/message/:id',
      name: 'MessageBox',
      component: () => import('../pages/home/Message'),
      meta: { title: '' }
    },
    /*  */
    {
      path: '/home/users',
      name: 'Users',
      component: () => import('../pages/home/Users'),
      meta: { title: '' }
    },
    /*  */
    {
      path: '/home/users/info',
      name: 'UsersCenter',
      component: () => import('../pages/home/UsersCenter'),
      meta: { title: '' }
    },
    /*  */
    {
      path: '/home/users/more',
      name: 'MoreInformation',
      component: () => import('../pages/home/MoreInformation'),
      meta: { title: '' }
    },
    /*  */
    {
      path: '/home/users/setting',
      name: 'Settings',
      component: () => import('../pages/home/Settings'),
      meta: { title: '' }
    },
    /*  */
    {
      path: '/home/setting/update/:param',
      name: 'UsersConfig',
      component: () => import('../pages/home/UsersConfig'),
      meta: { title: '' }
    },
    {
      path: '/admin/login',
      name: 'LoginManage',
      component: () => import('../pages/Login'),
      meta: { title: '' }
    },
    {
      path: '/admin',
      name: 'AdminManage',
      component: () => import('../pages/admin/Home'),
      children: [
        /*  */
        {
          path: 'index/:access_token?',
          name: 'HomeManage',
          component: () => import('../pages/admin/Index'),
          meta: { title: '' }
        },
        /*  */
        {
          path: 'auth/index',
          name: 'AuthManage',
          component: () => import('../pages/permission/Auth'),
          meta: { title: '' }
        },
        {
          path: 'role/index',
          name: 'RoleManage',
          component: () => import('../pages/permission/Role'),
          meta: { title: '' }
        },
        {
          path: 'permission/index',
          name: 'PermissionApply',
          component: () => import('../pages/permission/Apply'),
          meta: { title: '' }
        },
        /*  */
        {
          path: 'file/index',
          name: 'FileManage',
          component: () => import('../pages/file/File'),
          meta: { title: '' }
        },
        /*  */
        {
          path: 'push/index',
          name: 'PushManage',
          component: () => import('../pages/system/Push'),
          meta: { title: '' }
        },
        {
          path: 'config/index',
          name: 'SystemConfigManage',
          component: () => import('../pages/system/Config'),
          meta: { title: '' }
        },
        {
          path: 'database/index',
          name: 'DatabaseManage',
          component: () => import('../pages/system/Database'),
          meta: { title: '' }
        },
        {
          path: 'log/index',
          name: 'SystemLogManage',
          component: () => import('../pages/system/Log'),
          meta: { title: '' }
        },
        {
          path: 'area/index',
          name: 'AreaManage',
          component: () => import('../pages/system/Area'),
          meta: { title: '' }
        },
        {
          path: 'tools/index',
          name: 'SystemTools',
          component: () => import('../pages/system/Tools'),
          meta: { title: '' }
        },
        {
          path: 'plugin/index',
          name: 'PluginManage',
          component: () => import('../pages/system/Plugin'),
          meta: { title: '' }
        },
        /*  */
        {
          path: 'users/index',
          name: 'UsersManage',
          component: () => import('../pages/users/Users'),
          meta: { title: '' }
        },
        {
          path: 'userCenter/index',
          name: 'CenterManage',
          component: () => import('../pages/users/Center'),
          meta: { title: '' }
        },
        {
          path: 'oauth/index',
          name: 'OAuthManage',
          component: () => import('../pages/users/OAuth'),
          meta: { title: '' }
        },
        /*  */
        {
          path: 'interfaceCategory/index',
          name: 'InterfaceManage',
          component: () => import('../pages/interface/Interface'),
          meta: { title: '' }
        },
        /*  */
        {
          path: 'spider/index',
          name: 'SpiderManage',
          component: () => import('../pages/system/Spider'),
          meta: { title: '' }
        },
        /*  */
        {
          path: 'result/index',
          name: 'ResultManage',
          component: () => import('../pages/Result'),
          meta: { title: '' }
        }
      ]
    }
  ]
});
export default router;
