import { commonMethods } from '@/api/methods';
import URLS from '@/api/urls';

export const state = {
  imageLists: {
    index: { lists: [], total: 0 },
    search: { lists: [], total: 0, searchKeys: '' }
  },
  configuration: {
    notice: [],
    hotKeyWord: [],
    tags: []
  },
  menuLists: [
    { label: '', icon: 'el-icon-house', path: '/', highlight: ['HomeIndex'] },
    { label: '', icon: 'el-icon-search', path: '/home/search', highlight: ['HomeSearch'] },
    { label: '', icon: 'el-icon-chat-dot-square', path: '/home/chat', highlight: ['ConnectPerson', 'MessageBox'] },
    { label: '', icon: 'el-icon-user', path: '/home/users', highlight: ['Users', 'UsersCenter', 'Settings'] }
  ],
  accountSetting: [
    { label: '', icon: 'el-icon-user', path: '/home/users/info', value: 'account' },
    { label: '', icon: 'el-icon-setting', path: '/home/users/setting', value: 'setting' },
    { label: '', icon: 'el-icon-upload2', value: 'logout' },
    { label: '', icon: 'el-icon-loading', value: 'loading' }
  ],
  systemSetting: [
    { label: '', value: 1, class: 'grid-item settings' },
    { label: '', value: 1, class: 'grid-item' },
    { label: '', value: 1, class: 'grid-item' },
    { label: '', value: 1, class: 'grid-item' }
  ],
  /*  */
  moreInformationConfig: { ip_address: '', local: '', desc: '', tags: '', notice_status: '', u_name: '' },
  /*  */
  chatBody: {
    userLists: [],
    indexLists: []
  },
  /*  */
  receiver: {},
  /*  */
  userLists: [],
  /*  */
  messageLists: [],
  /*  */
  selfMessage: [],
  /*  */
  error: {
    /*  */
    success: { code: '20000', message: 'successfully' },
    /*  */
    login: { code: '20003', message: 'Please Login System' },
    /*  */
    cancel: { code: '40001', message: 'Cancel Oauth Login' }
  }

};
export const mutations = {
  /**
   * todo: vuex
   * @param state
   * @param update
   */
  UPDATE_MUTATIONS(state, update) {
    Object.keys(update).forEach(item => {
      state[item] = update[item];
    });
  }
};

export const actions = {
  /**
   * todo:
   * @param commit
   * @param state
   * @param payload
   * @returns {Promise<unknown>|boolean}
   */
  getImageLists({ commit, state }, payload) {
    return new Promise((resolve, reject) => {
      commonMethods(payload.value === 'index' ? URLS.image.list : URLS.image.hot, payload).then((result) => {
        switch (payload.type) {
          case 'index':
            commit('UPDATE_MUTATIONS', {
              imageLists: {
                index: {
                  lists: ((((result || {}).data || {}).item || {}).lists || {}).data || [],
                  total: ((((result || {}).data || {}).item || {}).lists || {}).total || 0
                },
                search: {
                  lists: state.imageLists.search.lists,
                  total: state.imageLists.search.total,
                  searchKeys: state.imageLists.search.searchKeys
                }
              }
            });
            break;
          case 'search':
            commit('UPDATE_MUTATIONS', {
              imageLists: {
                index: { lists: state.imageLists.index.lists, total: state.imageLists.index.total },
                search: {
                  lists: ((((result || {}).data || {}).item || {}).lists || {}).data || [],
                  total: ((((result || {}).data || {}).item || {}).lists || {}).total || 0,
                  searchKeys: payload.name
                }
              }
            });
            break;
          default:
            commit('UPDATE_MUTATIONS', {
              imageLists: {
                index: { lists: state.imageLists.index.lists, total: state.imageLists.index.total },
                search: {
                  lists: state.imageLists.search.lists,
                  total: state.imageLists.search.total,
                  searchKeys: state.imageLists.search.searchKeys
                }
              }
            });
            break;
        }
        resolve(result);
      }).catch(error => {
        commit('UPDATE_MUTATIONS', { error: (error.data || {}).item || {} }, { root: true });
        reject(error);
      });
    });
  },
  /**
   * todo:
   * @param commit
   * @param state
   * @param payload
   * @returns {Promise<unknown>|boolean}
   */
  getConfiguration({ commit, state }, payload) {
    return new Promise((resolve, reject) => {
      commonMethods(URLS.image.config, payload).then((result) => {
        switch (payload.type) {
          case 'notice':
            commit('UPDATE_MUTATIONS', {
              configuration: {
                notice: state.configuration.notice.length > 0 ? state.configuration.notice : (((result || {}).data || {}).item || {}).lists || [],
                hotKeyWord: state.configuration.hotKeyWord,
                tags: state.configuration.tags
              }
            });
            break;
          case 'hotKeyWord':
            commit('UPDATE_MUTATIONS', {
              configuration: {
                notice: state.configuration.notice,
                hotKeyWord: state.configuration.hotKeyWord.length > 0 ? state.configuration.hotKeyWord : (((result || {}).data || {}).item || {}).lists || [],
                tags: state.configuration.tags
              }
            });
            break;
          case 'tags':
            commit('UPDATE_MUTATIONS', {
              configuration: {
                notice: state.configuration.notice,
                hotKeyWord: state.configuration.hotKeyWord,
                tags:  state.configuration.tags.length > 0 ? state.configuration.tags : (((result || {}).data || {}).item || {}).lists || []
              }
            });
            break;
          default:
            commit('UPDATE_MUTATIONS', {
              configuration: {
                notice: state.configuration.notice,
                hotKeyWord: state.configuration.hotKeyWord,
                tags: state.configuration.tags
              }
            });
            break;
        }
        resolve(result);
      }).catch(error => {
        commit('UPDATE_MUTATIONS', { error: (error.data || {}).item || {} }, { root: true });
        reject(error);
      });
    });
  }
};

export default {
  namespaced: true,
  actions,
  mutations,
  state
};
